* {
  box-sizing: border-box;
}
.joke-box {
  padding: 2rem;
}

.joke-container {
  background-color: #1864ab;
  color: #f8f9fa;
  height: 40vh;
  max-width: 100%;
  font-size: 2rem;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
}

.joke-box-container {
  max-width: 120rem;
  display: flex;
  flex-direction: column;
  gap: 2rem;
}

.new-joke {
  background-color: #222;
  border: none;
  color: #fff;
  width: 20rem;
  height: 5rem;
}

@media (max-width: 44rem) {
  .joke-box {
    font-size: 1.6rem;
    height: 25vh;
    display: flex;
    align-items: center;
    justify-content: center;
  }
}
