html {
  font-size: 62.5%;
}

body {
  height: 100vh;
  font-family: sans-serif;
  width: 120rem;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  gap: 2rem;
  text-align: center;
}

.heading-primary {
  font-size: 3.8rem;
}

.selected-category {
  font-size: 1.6rem;
}

@media (max-width: 92rem) {
  body {
    width: 90%;
  }
}

@media (max-width: 44rem) {
  body {
    width: 90%;
    gap: 2.4rem;
  }
  .heading-primary {
    font-size: 2.4rem;
  }
}
